<!-- 散点气泡图 -->

<template>
  <div class="demo">
    <div class="box">
      <h3>支付宝流量桑基图</h3>
      <div id="container"></div>
    </div>
  </div>
</template>

<script>
import { Sankey } from '@antv/g2plot'

export default {
  components: {},
  data() {
    return {}
  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const DATA = [
      { source: '首次打开', target: '首页 UV', value: 160 },
      { source: '结果页', target: '首页 UV', value: 40 },
      { source: '验证页', target: '首页 UV', value: 10 },
      { source: '我的', target: '首页 UV', value: 10 },
      { source: '朋友', target: '首页 UV', value: 8 },
      { source: '其他来源', target: '首页 UV', value: 27 },
      { source: '首页 UV', target: '理财', value: 30 },
      { source: '首页 UV', target: '扫一扫', value: 40 },
      { source: '首页 UV', target: '服务', value: 35 },
      { source: '首页 UV', target: '蚂蚁森林', value: 25 },
      { source: '首页 UV', target: '跳失', value: 10 },
      { source: '首页 UV', target: '借呗', value: 30 },
      { source: '首页 UV', target: '花呗', value: 40 },
      { source: '首页 UV', target: '其他流向', value: 45 }
    ]

    const sankey = new Sankey('container', {
      data: DATA,
      sourceField: 'source',
      targetField: 'target',
      weightField: 'value',
      nodeWidthRatio: 0.008,
      nodePaddingRatio: 0.03
    })

    sankey.render()
  },
  methods: {},
  created() {
    // var wrapperElement = '<div style=position:relative;></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 400px;
  }

  #containerG2 {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



